  src: url('/static/fonts/opensans/OpenSans-Regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('/static/fonts/opensans/OpenSans-Regular-webfont.woff') format('woff'),
    url('/static/fonts/opensans/OpenSans-Regular-webfont.ttf') format('truetype'),
    url('/static/fonts/opensans/OpenSans-Regular-webfont.svg#open_sansregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
