/*
* FIXED for Font-Face Chrome Rendering
================================================================================ */
@media screen and (-webkit-min-device-pixel-ratio:0) {

  @font-face {
    font-family: 'opensans-semibold';
    src: url('/static/fonts/opensans/OpenSans-Semibold-webfont.svg#open_sanssemibold') format('svg');
  }

  @font-face {
    font-family: 'opensans-bold';
    src: url('/static/fonts/opensans/OpenSans-Bold-webfont.svg#open_sansbold') format('svg');
  }

}
